          align-items: center;
  height: 26px;
  width: 26px;
  border: none;
  border-radius: 5em;
  padding-right: 1em;
  padding-left: 1em;
  padding-top: 0.4em;
  padding-bottom: 0.4em;
  background-color: silver;
  color: white;
  cursor: pointer;
}

.searchbutton:hover, .searchbar button:hover {
  background-color: #a3a3a3;
}

.searchbutton:active, .searchbar button:active {
