.arrow-slide-button, .container-slides .arrow1, .container-slides .arrow2 {
  background: rgba(43, 43, 43, 0.8);
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  text-align: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  padding-right: 0.5em;
  padding-left: 0.2em;
  /*arrows inside the button*/
}

.arrow-slide-button .rectangle-inside1, .container-slides .arrow1 .rectangle-inside1, .container-slides .arrow2 .rectangle-inside1, .container-slides .arrow1 .rectangle1, .container-slides .arrow2 .rectangle1 {
  border-radius: 2em;
  width: 25px;
  height: 7px;
  background: white;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  margin-bottom: 0.4em;
}

.arrow-slide-button .rectangle-inside2, .container-slides .arrow1 .rectangle-inside2, .container-slides .arrow2 .rectangle-inside2, .container-slides .arrow1 .rectangle2, .container-slides .arrow2 .rectangle2 {
  border-radius: 2em;
  width: 25px;
  height: 7px;
  background: white;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.container-slides {
  background-color: #8d8d8d;
  background-repeat: no-repeat;
  background-size: cover;
  height: 90vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.container-slides .arrow1 {
  position: absolute;
  left: 15px;
}

.container-slides .arrow1:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-transition-duration: 0.2s;
          transition-duration: 0.2s;
  cursor: pointer;
}

.container-slides .arrow1:active {
  background: silver;
}

.container-slides .arrow2 {
  position: absolute;
  right: 15px;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.container-slides .arrow2:hover {
  -webkit-transform: scale(1.1) rotate(180deg);
          transform: scale(1.1) rotate(180deg);
  -webkit-transition-duration: 0.2s;
          transition-duration: 0.2s;
  cursor: pointer;
}

.container-slides .arrow2:active {
  background: silver;
}

